import {
	DELETE_BRUSHFORM,
    SUBMIT_BRUSHFORM,
    UPDATE_BRUSHFORM
} from '../../../mutation-types';
import {brushList} from '../../../business-util';  
const state = {
    formName:'brushForm',
    //-
    selectedText: "",
    //-ID
    selectedId: 1,
    //-
    listLabel:"",
    //
    brushList,
    //
    submitData:{}
}

const mutations = {
	[DELETE_BRUSHFORM](state) {
        state.selectedText='';
        state.selectedId=1;
        state.submitData={};
     },
    [SUBMIT_BRUSHFORM](state) {
       state.submitData= {
            "excludeCheatedIds":state.selectedId
        }
    },
    [UPDATE_BRUSHFORM](state,item){
        state.selectedText=item.text;
        state.selectedId=item.id;
    },
}
export default {
    state,
    mutations
}
    
                
                
                
                
                
                



