import {
	
	SWITCH_OUTLOCATIONCONSOLE
} from '../../../mutation-types';

const state = {
	title: '',
	//false|true
	switch: false, 
}

const mutations = {
	[SWITCH_OUTLOCATIONCONSOLE](state, sw) {
		state.switch = sw;
	}
}

export default {
	state,
	mutations
}


