import {
	SWITCH_PUTLOCATIONCONSOLE
} from '../../../mutation-types';

const state = {
	title: '',
	//false|true
	switch: false, 
}
const mutations = {
	[SWITCH_PUTLOCATIONCONSOLE](state, sw) {
		state.switch = sw;
	},
}

export default {
	state,
	mutations,
}


