export const getWeek = (one,two,idx) => {
    if(idx==1){
         let  stateTime=new Date(one),
              endTimeR=new Date(two),
              threeDate=30*24*60*60*1000;
              if(endTimeR*1-stateTime*1>threeDate){
                   return false
              }else{
                   return true
              }
    }else if(idx==2){
            let d1 = new Date();
            let d2 = new Date();
            d2.setMonth(0);
            d2.setDate(1);
            let rq = d1-d2;
            let s1 = Math.ceil(rq/(24*60*60*1000));
            let week = Math.ceil(s1/7);
            let oDate = new Date(),
                y1=one.split('-')[0],
                y2=two.split('-')[0],
                z1=one.split('-')[1],
                z2=two.split('-')[1],
                newyear=oDate.getFullYear();
               if(week<10){
                week='0'+week
               }
               let newWeek=newyear+'-'+week;
              if(y2-y1==0 && one<=two && one<newWeek && two<newWeek){
                  if(z2*1-z1*1+1>8){
                    return false
                  }else{
                    return true
                  }
              }else if(y2-y1==1 && one<=two && one<newWeek && two<newWeek){
                    if(z1.substr(0,1)==0){
                        z1=z1.substr(1,1)
                    }
                    if(z2.substr(0,1)==0){
                        z2=z2.substr(1,1)
                    }
                    function change(y1){
                        var dateArr=[];
                        for(var i=0;i<10;i++){
                                dateArr.push(2010+i*6)
                        }
                        if(dateArr.indexOf(y1*1)!=-1){
                              return 53
                        }else{
                              return 52
                        }
                    }
                    let s=change(y1);
                    if(s-z1*1+z2*1+1>8){
                       return false
                    }else{
                        return true
                    }
              }else{
                return false
              }
             
    }else if(idx==3){
                let timed=new Date(),
                    yeard=timed.getFullYear(),
                    s1=one.split('-')[0]*1,
                    s2=two.split('-')[0]*1,
                    x1=one.split('-')[1]*1,
                    x2=two.split('-')[1]*1,
                    monthd =timed.getMonth()+1;
                    if(monthd<10){
                        monthd='0'+monthd
                    }
                    let y=yeard+'-'+monthd;
                    if(s2-s1==0 && one<=two && one<y && two<y){
                          if(x2-x1>2){
                            return false
                          }else{
                            return true
                          }
                  }else if(s2-s1==1 && one<=two && one<y && two<y){
                      if(12-x1*1+x2*1>2){
                            return false
                        }else{
                            return true
                        }
                  }else{
                    return false
                  }

    }              
}



