import {
	SWITCH_PLACECONSOLE
} from '../../../mutation-types';

const state = {
	title: '',
	//false|true
	switch: false, 
}

const mutations = {//
	[SWITCH_PLACECONSOLE](state, sw) {
		state.switch = sw;
	}
}

export default {
	state,
	mutations
}


