import {
    SWITCH_EQUIPMENTPROCONSOLE
} from '../../../mutation-types';

const state = {
    title: '',
    //false|true
    switch: false, 
}
const mutations = {
    [SWITCH_EQUIPMENTPROCONSOLE](state, sw) {
        state.switch = sw;
    },
}

export default {
    state,
    mutations,
}


